.banner{
     /* background-image: linear-gradient(to right, #635cd0, #2a7aad ); #3027ae */
     background-image: url("../images/bg-1.avif");
     background-position: center;
     background-repeat: no-repeat;
     background-size: cover;
    height: 100vmin;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    color: white;
}

.banner-child>h1{
    margin: 2.3vmax;
    font-weight: 600;
    font-size: 3.5vmax;
    font-family: Roboto;
}


.banner-child{
   width: 28.7vmax;
}


.banner-child >p{
    font:600 4vmax "lucida Sans";
     /* 1.font-weight 2.font-size 3.font-family */
}

.banner-child >a>button{
    margin-bottom: 3vmax;
    cursor: pointer;
    background-color: white;
    border: 1px solid white;
    border-radius: 0;
    padding: 1vmax;
    transition: all 0.5s;
    width: 9vmax;
    font:500 1vmax Roboto;
}


.banner-child >a>button:hover{
    background-color: rgba(255,255,255,0);
    color: white;
}

#home-metro-text{
   font-size: 2.3rem;
}

.banner::after {
      content: "";
      height: 100vmin;
      width: 100vw;
      position: absolute;
      background-color: #effffe;
      top: 0;
      left: 0;
      max-width: 100%;
      clip-path: polygon(100% 54%, 0 100%, 100% 100%); 
      clip-path: polygon(98% 100%, 0% 100%, 100% 100%);
      clip-path: polygon(98% 100%, 0% 100%, 99% 86%);
      clip-path: polygon(100% 100%, 0% 100%, 100% 77%);
   
  }

  .homeHeading{
      text-align: center;
      font-family: Roboto;
      font-size: 2.4vmax;
      border-bottom: 1px solid rgba(21,21,21,0.5);
      width: 20vmax;
      padding: 1vmax;
      margin: 5vmax auto;
      color:rgba(0,0,0,0.7);
    }
              /* product stylling */
.container{
    display: flex;
    margin: 2vmax auto;
    width: 80vw;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 100%;

}

.productCard{
    width: 14vmax;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: rgb(48,48,48);
    margin:2vmax;
    transition: all  0.5s;
    padding-bottom: 0.5vmax;
}

.productCard >img{
    width: 14vmax;
}

.productCard >div{
    margin: 0.5vmax;
    display: flex;
    justify-content: flex-start;
}

.productCard >div>span{
    margin: 0.5vmax;
    font: 300 0.7vmax "Roboto";
}

.productCard>p{
    font-family: "Roboto";
    font-size: 1.2vmax;
    margin:1vmax 0.5vmax;
    margin-bottom: 0;
}

.productCard>span{
    margin: 0.5vmax 0.5vmax;
    color: #000000;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1vmax;
}

.productCard:hover{
    box-shadow: 0 0 5px rgba(15,15,15,0.26) !important;
    transform: translateY(-1vmax) !important;
    transition: all 1s linear !important;
}

@media screen and (max-width:600px){
    .productCard>p{
        font-size:1.7vmax;
    }

    .productCard>div{
        margin: 0vmax;
        display: block;
    }

    .productCard>span{
        font-size: 1.5vmax;
    }

    .productCard>div>span{
        margin: 0 0.5vmax;
        font:300 1vmax "Roboto";
    }

    .banner-child{
        width: 8rem;
     }

     #home-metro-text{
        font-size: 0.9rem;
        margin: 5px 0;
     }

     .banner-child>h1{
         font-size: 0.8rem;
     }

     .banner-child >a>button{

        font-size: 0.7rem;
     }
     
}